
import { defineComponent, computed, PropType } from 'vue'
import { ApiStatus } from '@/utils/enums/data-source'

export default defineComponent({
  name: 'DisplayApiStatus',
  props: {
    status: {
      type: String as PropType<ApiStatus>,
      default: ApiStatus.incomplete,
    },
    loadingText: {
      type: String,
      default: '匹配中',
    },
    successText: {
      type: String,
      default: '匹配成功',
    },
    failedText: {
      type: String,
      default: '未找到字段',
    },
    completedText: {
      type: String,
      default: '配置完成',
    },
    incompleteText: {
      type: String,
      default: '配置未完成',
    },
    optional: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const statusText = computed(() => {
      if (props.status === ApiStatus.loading) {
        return props.loadingText
      }

      if (props.status === ApiStatus.success) {
        return props.successText
      }

      if (props.status === ApiStatus.completed) {
        return props.completedText
      }

      if (props.optional) {
        return '可选'
      }

      if (props.status === ApiStatus.failed) {
        return props.failedText
      }

      if (props.status === ApiStatus.incomplete) {
        return props.incompleteText
      }

      return props.status
    })

    return {
      apiStatus: ApiStatus,
      statusText,
    }
  },
})
